import React from 'react';

const TabContents = props => {
    const onClick = () => {
        const {label, onClick} = props;
        onClick(label);
    }


    return (
        <>
            <li
                className={'inline-block'}
                onClick={onClick}
            >
                {props.label}
            </li>
        </>
    )
}

export default TabContents;